  .header__burger {
    width: 30px;
    height: 20px;
    cursor: pointer;
    z-index: 100;
    background-color: #0000;
    margin-left: auto;
    display: block;
    position: relative;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    height: 2px;
    background-color: #343434;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    width: 100%;
    top: 0;
    left: 0;
  }

  .header__burger:after {
    width: 100%;
    bottom: 0;
    right: 0;
  }

  .header__burger span {
    width: 100%;
    height: 2px;
    background-color: #343434;
    transition: all .4s;
    position: absolute;
    bottom: 9px;
    left: 0;
  }

  .header__burger.header__menu--active:before {
    width: 100%;
    top: 9px;
    transform: rotate(-45deg);
  }

  .header__burger.header__menu--active:after {
    width: 100%;
    bottom: 9px;
    transform: rotate(45deg);
  }

  .header__burger.header__menu--active span {
    opacity: 0;
    left: 100%;
  }

  .header__menu {
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: #f7f5f2;
    flex-direction: column;
    align-items: center;
    gap: 60px;
    padding: 150px 0 0;
    transition: top .3s;
    display: flex;
    position: fixed;
    top: -100%;
    left: 0;
    overflow-y: auto;
  }

  .header__menu.header__menu--active {
    top: 0;
  }
